@charset "utf-8";

@-ms-viewport {
  width: device-width;
}
@-o-viewport {
  width: device-width;
}
@-moz-viewport {
  width: device-width;
}
@-webkit-viewport {
  width: device-width;
}
@viewport {
  width: device-width;
}

body {
  background: #fff;
  font-size: 12px;
  font-weight: normal;
  padding: 0px;
  margin: 0px;
  letter-spacing: -0px;
}
html,
body {
  width: 100%;
  height: 100%;
  letter-spacing: 0px;
}
div,
p,
ul,
li,
table,
th,
td,
textarea,
input,
span,
form,
h1,
h2,
h3,
h4,
h5,
h6,
dt,
dl,
dd,
em,
sup {
  padding: 0px;
  margin: 0px;
}
ul,
ol,
dl,
dd,
dt,
li {
  list-style-type: none;
  margin: 0;
  padding: 0px;
  line-height: 100%;
  font-size: 12px;
  letter-spacing: 0px;
}
form,
fieldset,
p {
  margin: 0;
  padding: 0;
  border: 0;
}
img {
  border: 0;
  vertical-align: middle;
  outline: none;
}

a,
a:link,
a:visited,
a:active {
  color: #575757;
  letter-spacing: normal;
  text-decoration: none;
  letter-spacing: -0.8px;
}
a:hover {
  color: #575757;
  text-decoration: none;
}
button {
  background: inherit;
  border: none;
  box-shadow: none;
  border-radius: 0;
  padding: 0;
  overflow: visible;
  cursor: pointer;
}

body {
  font-weight: 400;
  font-family: "Pretendard";
}

* {
  box-sizing: border-box;
}
